<template>
   <el-form label-width="0" label-position="top">
      <el-form-item label="Варианты"/>
      <el-form-item 
         v-for="(option, index) of value"
         :key="index"
      >
         <el-input 
            :value="value[index]"
            @change="emit($event, index)"
         >
            <el-button
               type="danger"
               icon="el-icon-remove-outline"
               slot="append"
               @click="delOpt(index)"
            />
         </el-input>
      </el-form-item>
      <el-button 
         type="success" 
         icon="el-icon-plus" 
         @click.prevent="$emit('addOpt')"
      >
         Добавить
      </el-button>
   </el-form>
</template>

<script>
   export default {
      props:['value'],
      methods: {
         emit (v, i) {
            this.$emit('changeOpt', {
               val: v,
               index: i
            })
         },
         delOpt (v) {
            this.$emit('delOpt', v)
         }
      }
   }
</script>
