                  shadow="hover"
               >
                  {{item.name}}
                  <el-button 
                     type="danger" 
                     icon="el-icon-delete" 
                     size="mini" 
                     circle
                     @click.prevent="removeComp(index)"
                  />
               </el-card>
            </draggable>
         </div>
      </div>
