      {{test}}
   </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters, mapMutations } from 'vuex'

export default {
   components: {
      draggable
   },
   data:()=>({
      activePanel: 'toolbar'
   }),
   computed: {
      ...mapGetters({
         components: 'getComponents',
         userComponents: 'getUserComponents',
         test: 'getRootValue'
      })
   },
   methods: {
      ...mapMutations({
          removeComp: 'delUserComponents'
      })
   }
}
</script>

<style lang="scss" scoped>

   #side-panel {
      height: 100%;
      min-width: 400px;
      max-width: 400px;
      width: 400px;
      
      box-sizing: border-box;
   }

   .toolbar {
      background-color: #b3b3b3;
      &-item {
         margin: 5px;
      }
   }

   .accordion {
      background-color: #BBBDF1;

      &-header {
         height: 48px;
         background-color: #BBBDF1;
         color: #fff;
         padding: 0 25px;
         box-sizing: border-box;
         display: flex;
         justify-content: space-between;
         align-items: center;
         cursor: pointer;
         font-size: 18px;
      }

      &-content {
         background-color: #fff;
         padding: 20px;
      }
   }
</style>