<template>
   <div id="tooltip">
      <div>
         <el-button 
            type="success" 
            icon="el-icon-upload2" 
            size="mini" 
            plain
            @click.prevent="emit('save')"
         />
         <el-button 
            type="primary" 
            icon="el-icon-edit" 
            size="mini" 
            plain 
            @click.prevent="emit('editValue')"
         />
         <el-button 
            type="warning" 
            icon="el-icon-setting" 
            size="mini" 
            plain
            @click.prevent="emit('editStyle')"
         />
      </div>
      <el-button
         type="danger" 
         icon="el-icon-delete" 
         size="mini" 
         plain
         @click.prevent="emit('del')"
      />
   </div>
</template>

<script>
export default {
   methods: {
      emit(n){
         this.$emit('push', {
            pos: [],
            name: n,
            val: ''
         })
      }
   }
}
</script>

<style scoped>
   #tooltip {
      position: absolute;
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      box-sizing: border-box;
      align-items: center;
      top: -36px;
      left: 0;
      width: 100%;
      z-index: 1000;
      height: 36px;
      min-width: 200px;
      background-color: #BBBDF1;
      outline: 3px solid #BBBDF1;
      overflow: visible;
   }
</style>