<template>
   <el-form label-width="180px">
      <el-form-item label="Фон">
         <el-checkbox v-model="checked"/>
      </el-form-item>
      <el-form-item label="Цвет фона" v-if="checked">
         <el-color-picker v-model="color"/>
      </el-form-item>
   </el-form>
</template>

<script>
export default {
   props: ['value'],
   data: ()=>({
      checked: false
   }),
   computed: {
      color: {
         get () {
            let color
            if (this.value === "inherit") 
               color = null
            else 
               color = this.value
            return color
         },
         set (v) {
            this.$emit('change', v)
         }
      }
   }
}
</script>